'use client';
import { useEffect, useMemo, useState } from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { useScanner } from '@/lib/useScanner';
import { progress } from '@/lib/api';
import { useToast } from '@/app/components/Toast';

export default function ScanKiosk() {
  const sp = useSearchParams();
  const kiosk = useMemo(() => sp.get('kiosk') === '1', [sp]);
  const router = useRouter();
  const { toast } = useToast();

  const [last, setLast] = useState<string>('');
  const [status, setStatus] = useState<string>('');

  // Enter/exit kiosk styles + fullscreen
  useEffect(() => {
    if (!kiosk) return;
    document.body.classList.add('kiosk');
    (async () => {
      try { if (!document.fullscreenElement) await document.documentElement.requestFullscreen(); } catch {}
      // @ts-ignore
      try { if (navigator.wakeLock?.request) { /* @ts-ignore */ await navigator.wakeLock.request('screen'); } } catch {}
    })();
    return () => document.body.classList.remove('kiosk');
  }, [kiosk]);

  useScanner(async (tag) => {
    setLast(tag);
    setStatus('');
    try {
      const res = await progress(tag);
      if (!res?.ok) throw new Error(res?.error || 'Could not progress');
      if (res.nextStatus === 'Processing') {
        toast(`Tag ${tag} â†’ Processing`, 'ok');
        router.push(`/butcher/intake?tag=${encodeURIComponent(tag)}`);
      } else if (res.nextStatus === 'Finished') {
        toast(`Tag ${tag} â†’ Finished`, 'ok');
        setStatus('Marked Finished âœ“');
        setTimeout(()=> setStatus(''), 1200);
      } else {
        setStatus('No change');
      }
    } catch (e:any) {
      setStatus(e?.message || 'Scan failed');
      toast('Scan failed', 'err');
    }
  });

  return (
    <main className="page-wrap" style={{textAlign:'center'}}>
      <h1 style={{margin:'4px 0 8px'}}>Scan a Tag</h1>
      <p className="muted" style={{margin:'0 0 16px'}}>
        {kiosk ? 'Kiosk mode: TV-ready, opens the butcher view automatically.' : 'This screen will open the butcher view automatically.'}
      </p>
      <div style={{fontSize: kiosk?64:48, fontWeight:900, letterSpacing:1, margin:'12px 0'}}>{last || 'â€”'}</div>
      <div style={{height:20, fontWeight:700, color: status.includes('âœ“') ? '#065f46' : '#b91c1c'}}>{status}</div>
    </main>
  );
}


